@import '../../styles/variables';

.imageShadow{
    width: 100%;
    border-radius: 30px 0px 30px 0px;
    overflow: hidden;
    @include flex;
    img{
        width: 100%;
    }
}
.left{
    box-shadow: 37px 44px 0px -11px #00ab6b;
}
.rigth{
    box-shadow: -37px -44px 0px -11px #00ab6b;
}